"use client";
import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';

export default function Shop() {
  const router = useRouter();
  const [selected, setSelected] = useState(null);
  const [showOrderForm, setShowOrderForm] = useState(false);
  
  // Sample product data
  const products = [
    { id: 1, name: 'T-Shirt 1', price: '₹499', category: 't-shirts', image: '/img/Design_1.jpeg' },
    { id: 2, name: 'Hoodie', price: '₹899', category: 'hoodies', image: '/img/Design_2.jpeg' },
  ];

  // Handle order now button click
  const handleOrderClick = (e, product) => {
    e.preventDefault();
    e.stopPropagation();
    setSelected(product);
    setShowOrderForm(true);
  };

  // Close modal
  const closeModal = (e) => {
    if (e) {
      e.preventDefault();
      e.stopPropagation();
    }
    setShowOrderForm(false);
    setSelected(null);
  };

  // Prevent event bubbling
  const stopPropagation = (e) => {
    e.stopPropagation();
  };

  return (
    <div className="min-h-screen bg-gray-100 pt-20 px-4">
      <div className="max-w-7xl mx-auto">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-3xl font-bold">Our Products</h1>
          <div className="flex space-x-4">
            <Link href="/" className="px-4 py-2 bg-gray-800 text-white rounded hover:bg-gray-700">
              Home
            </Link>
            <Link href="/contact" className="px-4 py-2 bg-gray-800 text-white rounded hover:bg-gray-700">
              Contact
            </Link>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {products.map((product) => (
            <div 
              key={product.id} 
              className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow"
            >
              <img 
                src={product.image} 
                alt={product.name}
                className="w-full h-64 object-cover"
              />
              <div className="p-4">
                <h3 className="text-lg font-semibold">{product.name}</h3>
                <div className="mt-4 flex justify-between items-center">
                  <button
                    onClick={(e) => handleOrderClick(e, product)}
                    className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
                  >
                    Order Now
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Order Modal */}
      {showOrderForm && selected && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50"
          onClick={closeModal}
        >
          <div 
            className="bg-white rounded-lg p-6 w-full max-w-md"
            onClick={stopPropagation}
          >
            <h2 className="text-2xl font-bold mb-4">Order {selected.name}</h2>
            <form onSubmit={(e) => {
              e.preventDefault();
              alert(`Order placed for ${selected.name}`);
              closeModal();
            }}>
              <div className="mb-4">
                <label className="block text-sm font-medium mb-1">Name</label>
                <input 
                  type="text" 
                  required 
                  className="w-full p-2 border rounded"
                />
              </div>
              <div className="mb-4">
                <label className="block text-sm font-medium mb-1">Phone</label>
                <input 
                  type="tel" 
                  required 
                  className="w-full p-2 border rounded"
                />
              </div>
              <div className="flex justify-end space-x-4">
                <button 
                  type="button" 
                  onClick={closeModal}
                  className="px-4 py-2 border rounded"
                >
                  Cancel
                </button>
                <button 
                  type="submit" 
                  className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
                >
                  Place Order
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
